"use client";

import React from "react";
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

interface LatestTransaction {
  noTransaksi: string;
  tanggal: string;
  anggota: string;
  jenis: string;
  jumlah: string;
  status: string;
}

const LatestTransactionsTable = ({ data }: { data: LatestTransaction[] }) => {
  return (
    <Table>
      <TableCaption>Transaksi Terbaru</TableCaption>
      <TableHeader>
        <TableRow>
          <TableHead className="w-[100px]">No. Transaksi</TableHead>
          <TableHead>Tanggal</TableHead>
          <TableHead>Anggota</TableHead>
          <TableHead>Jenis</TableHead>
          <TableHead>Jumlah</TableHead>
          <TableHead>Status</TableHead>
        </TableRow>
      </TableHeader>
      <TableBody>
        {data.map((item) => (
          <TableRow key={item.noTransaksi}>
            <TableCell className="font-medium">{item.noTransaksi}</TableCell>
            <TableCell>{item.tanggal}</TableCell>
            <TableCell>{item.anggota}</TableCell>
            <TableCell>{item.jenis}</TableCell>
            <TableCell>{item.jumlah}</TableCell>
            <TableCell>{item.status}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};

export default LatestTransactionsTable;
